.about {
  padding: 20vh 0;
  display: -ms-flexbox;
  display: flex;
}
.about > img {
  margin: 3em;
}
.about > img:hover {
  border: 3px solid black;
}

.bio_text {
  -ms-flex-order: 2;
      order: 2;
  margin: 3rem 3em 0em;
  padding: 0rem 5em 0em
}

@media screen and (max-width: 1300px) {
  .about {
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 30vh 0 0;
  }
  .about>img {
    height: 30vh;
    margin: 5em 2em 0;
  }
  .bio_text {
    margin: 3rem 1em 0em;
  }
  
}

@media screen and (max-width: 800px){
    .about {
      -ms-flex-direction: row;
          flex-direction: row;
      padding: 16vh 0 0;
    }
    .about > img {
      height: 20vh;
      margin: 7em 3em;
    }
    .bio_text {
      margin: 0em -2em 3em
    }
}

@media screen and (max-width: 500px) {
  .about {
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 16vh 0 0;
  }
  .about>img {
    height: 36vh;
    margin: 4em 3em 0;
  }
  .bio_text {

  }
}



