.projects {
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-direction: row;
      flex-direction: row;
  max-height: 100vh;
  padding: 10em;
  border: black 10px;
}

.col {
  -ms-flex: 1 1;
      flex: 1 1;
  -webkit-box-shadow: 0px 12px 36.8px 9.2px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 12px 36.8px 9.2px rgba(0, 0, 0, 0.1);
  -webkit-animation: fadeUp 2s;
          animation: fadeUp 2s;
  margin: 10px;
  -webkit-box-shadow: 3px 3px 8px 0px rgba(0, 0, 0, 0.3);
          box-shadow: 3px 3px 8px 0px rgba(0, 0, 0, 0.3);
  max-width: 23vw;
}

@media screen and (max-width: 1000px){
    .projects {
        -ms-flex-direction: column;
            flex-direction: column;
        padding: 10em 0;
        -ms-flex-wrap: nowrap;
            flex-wrap: nowrap;
    }
    .col {
          -webkit-animation: fadeUp 2s;
                  animation: fadeUp 2s;
          margin: 10px;
          max-width: none;
          -ms-flex: 0 0;
              flex: 0 0; 
    }
}




